.like_item {
  position: relative;
  width: 300px;
  height: 200px;
  background-color: #f2f2f2;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  perspective: 1000px;
  box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.5019607843);
  transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin: 10px;
}
.like_item img {
  width: 35%;
}
.like_item:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(255, 255, 255, 0.2);
}
.like_item:hover .item_card {
  transform: rotateX(0deg);
}
.like_item .item_card {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 30px;
  box-sizing: border-box;
  -webkit-backdrop-filter: blur(25px);
          backdrop-filter: blur(25px);
  transform: rotateX(-90deg);
  transform-origin: bottom;
  transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.like_item .item_card .card_title {
  margin: 0;
  font-size: 20px;
  color: #024d8b;
  font-weight: 700;
  text-align: center;
}
.like_item .item_card .card_content {
  text-align: center;
  margin: 10px 0 0;
  font-size: 16px;
  color: black;
  line-height: 1.4;
}
.like_item .item_card .card_price {
  text-align: end;
  color: firebrick;
}
.like_item .item_card .like_item_bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.like_item .item_card .like_item_bottom .plus_btn {
  all: unset;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 8px 8px;
  border-radius: 50%;
  border: 1px solid #ccc;
  background-color: white;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
  cursor: pointer;
}
.like_item .item_card .like_item_bottom .plus_btn .plus-icon {
  width: 20px;
  height: 20px;
  stroke: #333;
  transition: fill 1s ease;
}
.like_item .item_card .like_item_bottom .plus_btn:hover {
  background-color: #f0f0f0;
}
.like_item .item_card .like_item_bottom .plus_btn:active {
  transform: scale(0.95);
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}/*# sourceMappingURL=LikeItem.css.map */